
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: left;
      -ms-flex-pack: left;
          justify-content: left;
  width: 100%;
}
.grid__item {
  width: 100%;
  padding: 0 6px 12px;
}
/* XS and up */
@media (min-width: 576px) {
.grid__item {
    max-width: 50%;
}
}
/* SM and up */
@media (min-width: 768px) {
.grid__item {
    max-width: 33%;
}
}
/* MD and up */
@media (min-width: 992px) {
.grid__item {
    max-width: 25%;
}
}
.grid__item .card {
  width: 100%;
}
.searchbar {
  width: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}



































































